.project-card{
    background-color: #202022;
    transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
    border-top-right-radius: 30px;
    border-bottom-left-radius: 30px;
    z-index: 1000;
}

.project-card .project-stack {
    display: inline-block;
    position: relative;
}

.project-card .project-stack::after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #0087ca;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.project-card:hover {
    transform: translateY(-5px) scale(1.005) translateZ(0);
    box-shadow: 0 24px 36px rgba(0, 0, 0, 0.11),
    0 24px 46px rgba(26, 26, 26, 0.11);
    border-radius: 0;
}

.project-card:hover .project-name {
    transition: color .3s linear, box-shadow .3s linear;
    color: rgb(202, 80, 202);
}

.project-card:hover .project-stack::after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.project-card:active {
    transform: scale(1) translateZ(0);
    box-shadow: 0 15px 24px rgba(18, 216, 255, 0.11),
      0 15px 24px rgba(184, 249, 211, 0.48);
}